.zindex-1{
	z-index: 1;
}

hr {
	display: block;
	height: 2px;
	border: 0;
	border-top: 2px solid $primary-color;
	margin: 1em 0;
	padding: 0;
}

.icon-box{
	height: 80px;
	width: 80px;
	text-align: center;
	background: $primary-color;
	i{
		line-height: 80px;
		font-size: 30px;
	}
}

.slider-container {
	max-width: 1200px;
	width: 100%;
	background: transparent;
	border-radius: 20px;
	// padding: 1rem 40px;
	padding: 1rem 0;
	box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
	opacity: 0.8;
	@include tablet {
			position: fixed;
			top: 4rem;
			left: 0;
			opacity: 0.2;
		}
}

.slider-wrapper {
	position: relative;
	overflow: hidden;
	border-radius: 15px;
}

.slider-track {
	display: flex;
	// gap: 2rem;
	transition: transform 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.slide {
	min-width: 100%;
	// padding: 2rem;
	flex-shrink: 0;
}

.slide img {
	width: fit-content;
	height: auto;
	margin: auto;
	object-fit: cover;
	display: block;
	border-radius: 15px;
}

.controls {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 20px;
	margin-top: 30px;
}

.nav-btn {
	width: 50px;
	height: 50px;
	border: none;
	border-radius: 50%;
	background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
	color: white;
	font-size: 20px;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: all 0.3s ease;
	box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
}

.nav-btn:hover {
	transform: scale(1.1);
	box-shadow: 0 6px 20px rgba(102, 126, 234, 0.6);
}

.nav-btn:active {
	transform: scale(0.95);
}